import { Component, OnInit, OnChanges, SimpleChanges, Input } from '@angular/core';
import { EditDataService } from '../../edit.data.service';
import { SharedServices } from '../../../../../shared/services/services.module';
import { LeftPanelService } from '../left-panel/left-panel.service';
import { LogoService } from '../../survey.main-list/logo.service';

@Component({
  selector: 'theme-survey-list',
  templateUrl: './theme-survey-list.component.html',
  styleUrls: ['./theme-survey-list.component.scss']
})
export class ThemeSurveyListComponent implements OnInit, OnChanges {
  @Input() themeId: string;
  surveyData: any;
  logoDetails: any;
  themeCss: any;

  constructor(
  	private services: SharedServices,
    private editDataService: EditDataService,
    private lpService: LeftPanelService,
    private logoService: LogoService
  ) {

  }

  ngOnInit() {
    let sid = this.editDataService.sId;
    this.logoDetails = this.logoService.logoDetails;
    this.services.form.getSurveyData(sid).subscribe(data => {
      if(!data) return;
      this.surveyData = this.services.form.formObj;
      this.logoService.setLogoOnLoad(sid, this.surveyData);
    });

    this.lpService.themeCompState.subscribe(data => {
      if(!data) return;
      if(data.field && 'survey' !== data.field) {
        return;
      }
      this.themeCss = this.lpService.getThemeCssObj();
    })
  }

  ngOnChanges(changes: SimpleChanges){
    this.themeCss = this.lpService.getThemeCssObj();
  }

}



