import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'theme-text-align',
  templateUrl: './text-align.component.html',
  styleUrls: [
    './text-align.component.scss',
    '../../left-panel.component.scss'
  ]
})
export class TextAlignComponent implements OnInit {
  @Input() textAlignInp: any;
  @Output() emitTextAlign: EventEmitter<any> = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {
  }

  onChangeAlignment(align: string, e: any){
    this.textAlignInp.textAlign = (e.target.checked) ? align : '';
    this.emitTextAlign.emit(this.textAlignInp.textAlign);
  }

}



