import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { LeftPanelService } from '../../left-panel/left-panel.service';
import { SharedServices } from '../../../../../../shared/services/services.module';

declare var PageTheme;

@Component({
  selector: 'theme-page-list',
  templateUrl: './theme-page-list.component.html',
  styleUrls: ['./theme-page-list.component.scss']
})
export class ThemePageListComponent implements OnInit, OnChanges {
  @Input() themeId: string;
  @Input() pageObj: any;

  setsBtn: any;
  pageTheme: any;
  formObj: any;
  pageData: any;
  themeSet: any;
  themeJSON: any;

  constructor(
    private lpService: LeftPanelService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.formObj = this.services.form.formObj;
    let allPagesAndQues = this.services.form.getAllPagesAndQuestions();
    this.pageData = allPagesAndQues['data'][this.pageObj._id];
    this.applyTheme();
    this.setsBtn = this.lpService.themeSetProperties.rightSideSetsBtn;
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.lpService.getThemeCssObj()) {
      this.themeJSON = this.lpService.themeJSON;
      let pageThemeObj = this.lpService.themeJSON.page;
      if(this.pageObj.themeSetId) {
        // if(this.services.form.formObj.theme.pageSet && this.services.form.formObj.theme.pageSet[this.pageObj.themeSetId]) {
          // let pageSetTheme = this.services.form.formObj.theme.pageSet[this.pageObj.themeSetId];
        if(this.lpService.themeJSON.ifSetExist(this.pageObj.themeSetId)){
          let pageSetTheme = this.lpService.themeJSON.getThemeSet(this.pageObj.themeSetId);
          pageThemeObj = this.services.utils.extendObject(pageThemeObj, new PageTheme(pageSetTheme));
        }
      }

      this.pageTheme = this.services.form.formObj.theme.getFieldCssObj(pageThemeObj)
    }
  }

  applyTheme() {
    this.lpService.themeCompState.subscribe(data => {
      if(!data) return;
      this.themeJSON = this.lpService.themeJSON;
      if(data.field && 'page' !== data.field) {
        return;
      }
      let themeSetId = this.services.form.formObj.getPageById(this.pageObj._id).themeSetId;
      let setExists = this.lpService.themeJSON.ifSetExist(themeSetId);
      if(data.forAll && !setExists) {
        this.pageTheme = this.lpService.getThemeCssObj().page;
      } else if (data.id == this.pageObj._id) {
        this.pageTheme = this.lpService.themeSet[data.field];
      }
    });
  }

  showPageSets(id: string, e: any){
    e.stopPropagation();
    this.lpService.setComponent('page-sets', id);
  }

}



