import { Component, OnInit, Output, EventEmitter, OnDestroy, Pipe, PipeTransform } from '@angular/core';
import { LeftPanelService } from '../left-panel.service';
import { EditDataService } from '../../../edit.data.service';
import { SharedServices } from '../../../../../../shared/services/services.module';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'theme-themes-list',
  templateUrl: './themes-list.component.html',
  styleUrls: ['./themes-list.component.scss', '../left-panel.component.scss']
})
export class ThemesListComponent implements OnInit, OnDestroy {
  @Output() onThemeApply: EventEmitter<any> = new EventEmitter<any>();
  themeType: number;
  themeArr: any;
  triggerPipeAgain: boolean = false;
  activeThemeId: string;
  previewThemeId: string;
  themeData: any;
  themeOpt: any = {
    skip: 0,
    limit: 100000,
    sortParam: 'created',
    sortOrder: 1,
    tName: '',
    // default: null
  }
  subscription: Subscription;

  constructor(
    private editDataService: EditDataService,
    private leftPanelService: LeftPanelService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.themeArr = this.leftPanelService.allThemesArr;
    if(!this.leftPanelService.allThemesArr.list.length){
      this.filterThemes();
    }
    this.subscription = this.services.form.getSurveyData(this.editDataService.sId).subscribe(data => {
      if(!data) return;
      if(this.services.form.formObj.theme) {
        this.activeThemeId = this.services.form.formObj.theme._id;
        if(this.leftPanelService.previewThemeId){
          this.previewThemeId = this.leftPanelService.previewThemeId;
          this.leftPanelService.previewThemeId = '';
        }else{
          this.previewThemeId = this.services.form.formObj.theme._id;
        }
        this.themeType = !!this.services.form.formObj.theme.default ? 1 : 0;
      } else {
        // set default theme id
      }
      this.applyThemeToSurvey(this.previewThemeId);
    });
  }

  customiseTheme(e: any, themeItem?: any){
    e.stopPropagation();
    if(themeItem && this.services.form.formObj.theme._id != this.previewThemeId){
      this.services.toastr.warning('Please apply the theme before it could be customised');
      return false;
    }
    let themeData: any = themeItem ? this.services.utils.cloneObject(themeItem) : undefined;
    themeData && (themeData = this.updateForDefaultTheme(themeData));
    this.leftPanelService.buildThemeData(themeData);
    this.leftPanelService.themeSettings.presentAction = 'theme-customisation';
    this.editDataService.surveyDesignData.displaySidebar = false;
    if(!themeItem || themeItem.default){
      this.leftPanelService.checkEventBinding(true);
    }else{
      this.leftPanelService.checkEventBinding();
    }
    !themeItem && this.onThemeApply.emit('');
  }

  updateForDefaultTheme(themeData: any) {
    if(themeData.default) {
      themeData.themeParent = themeData._id;
      delete themeData._id;
      themeData.default = false;
      themeData.name = themeData.name + '_copy';
    }
    return themeData;
  }

  previewTheme(themeData: any){
    this.previewThemeId = themeData._id;
    // this.themeData = themeData;
    this.applyThemeToSurvey(themeData._id);
  }

  saveThemeToSurvey(){
    let sId = this.services.form.formObj._id;
    let themeData: any = {
      theme: this.previewThemeId
    }
    this.services.resource.updateSurveyData(sId, themeData).subscribe((data) =>{
      this.activeThemeId = this.previewThemeId;
      this.applyThemeToSurvey(this.activeThemeId);
      this.services.form.formObj['theme'] = new Theme(this.themeData);
      this.services.toastr.success('Theme has been applied successfully.');
    })
  }

  applyThemeToSurvey(themeId?: string){
    if(themeId) {
      this.services.resource.getThemeById(themeId).subscribe((data) => {
        this.themeData = data.data;
        this.leftPanelService.buildThemeData(data.data);
        this.onThemeApply.emit(data.data._id);
        let idx = this.services.utils.findIndex(this.themeArr.list, '_id', data.data._id);
        if(idx == -1){
          this.themeArr.list.splice(0, 0, data.data);
          this.triggerPipeAgain = !this.triggerPipeAgain;
        }
      })
    } else {
      this.leftPanelService.buildThemeData();
    }
  }

  delTheme(theme: any, e: any){
    e.stopPropagation();
    this.services.alert({
      title: '<p class="sweetalert_title">Are you sure you want to delete this theme?</p>',
      text: "",
      type: 'warning',
      showCancelButton: true,
      buttonsStyling: false,
      confirmButtonClass: 'sweetalert_apply_btn',
      cancelButtonClass: 'sweetalert_cancel_btn',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if(result.value){
        this.deleteTheme(theme);
      }
    })
  }

  deleteTheme(theme: any){
    let themeId: string = theme._id;
    let replaceTheme: string = null;
    if(themeId == this.activeThemeId){
      if(theme.themeParent){
        replaceTheme = theme.themeParent;
      }else{
        replaceTheme = this.themeArr.list.filter((theme) => {return theme.default})[0]._id;
      }
    }
    this.services.resource.deleteTheme(themeId, replaceTheme).subscribe((data) => {
      let idx: number = this.services.utils.findIndex(this.themeArr.list, '_id', themeId);
      this.themeArr.list.splice(idx, 1);
      this.themeArr.count--;
      this.triggerPipeAgain = !this.triggerPipeAgain; //changing inputs of pipe to trigger it again
      this.services.toastr.success('Theme has been deleted successfully.','Success');
      if(replaceTheme){
        this.getNewTheme(replaceTheme);
      }
    })
  }

  getNewTheme(replaceThemeId: string){
    this.services.resource.getThemeById(replaceThemeId).subscribe((data) => {
      this.services.form.formObj.theme = new Theme(data.data);
      this.activeThemeId = replaceThemeId;
      this.previewThemeId = this.activeThemeId;
      this.themeType = !!this.services.form.formObj.theme.default ? 1 : 0;
      this.applyThemeToSurvey(this.activeThemeId);
    })
  }

  filterThemes(){
    this.services.resource.getAllThemes(this.themeOpt).subscribe((data) => {
      if(data && data.data){
        this.leftPanelService.allThemesArr.list = data.data.list;
        this.leftPanelService.allThemesArr.count = data.data.total;
      };
    });
  }

  sortThemes(){
    this.themeOpt.sortParam = 'name';
    this.themeOpt.sortOrder = (this.themeOpt.sortOrder == 1) ? -1 : 1;
    this.filterThemes();
  }

  // loadMore(){
  //   this.themeOpt.skip++;
  //   this.themeOpt.default = false;
  //   this.services.resource.getAllThemes(this.themeOpt).subscribe((data) => {
  //     if(data && data.data){
  //       this.leftPanelService.allThemesArr.list.concat(data.data.list);
  //       this.leftPanelService.allThemesArr.count = data.data.total;
  //       this.triggerPipeAgain = !this.triggerPipeAgain;
  //     };
  //   });
  // }

  ngOnDestroy(){
    this.subscription.unsubscribe();
  }

}

@Pipe({
  name: 'filterTheme'
})
export class FilterTheme implements PipeTransform {

  transform(themeArr: any[], isDefault: number, triggerPipeAgain: boolean): any[] {
    let filteredThemeArr = themeArr.filter( item => {
      return !!item.default == !!isDefault;
    });
    return filteredThemeArr;
  }
}



