import { Component, OnInit } from '@angular/core';

import { LeftPanelService } from '../left-panel.service';
import { SharedServices } from '../../../../../../shared/services/services.module';

declare var PageTheme;

@Component({
  selector: 'theme-page-setting',
  templateUrl: './page-setting.component.html',
  styleUrls: ['./page-setting.component.scss', '../left-panel.component.scss']
})
export class PageSettingComponent implements OnInit {
  themeSet: any;
  themeAction: any; //to let you know it is a save or update operation
  themeName: string;
  appliedSet: any;
  forAll: boolean = true;

  constructor(
  	private leftPanelService: LeftPanelService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.themeName = this.leftPanelService.themeJSON.name;
    this.themeAction = this.leftPanelService.themeSetProperties.action;
    this.appliedSet = {};
    if(this.themeAction.new){
      this.forAll = false;
      this.leftPanelService.themeSet['page'] = new PageTheme();
    }else{
      this.leftPanelService.themeSet['page'] = this.leftPanelService.themeJSON.page;
      this.appliedSet['themeSetId'] = this.leftPanelService.themeSettings.activeElem['themeSetId'];
      if(this.appliedSet['themeSetId']) {
        this.forAll = false;
        let setData = this.services.utils.cloneObject(this.leftPanelService.themeJSON.getPageSetById(this.appliedSet['themeSetId']));
        this.leftPanelService.themeSet['page'] = new PageTheme(setData);
      }
    }
    this.themeSet = !this.forAll ? this.leftPanelService.themeSet : this.leftPanelService.themeJSON;
    // !this.themeSet.page.body.fontSize && (this.themeSet.page.body.setFontSize("14px"));
    !this.themeSet.page.body.fontSize && (this.themeSet.page.body.fontSize = "14px");
  }

  onChange(event: any){
    event['forAll'] = this.forAll;
    this.leftPanelService.callNextOnThemeCompState(event);
  }

  updateTheme() {
    this.leftPanelService.updateTheme(this.forAll).subscribe( data => {
      this.cancelCustomisation();
    });
  }

  cancelCustomisation(){
    this.leftPanelService.onCancelSetting();
    this.leftPanelService.switchToThemeCust();
  }

  onChangeForAll() {
    this.leftPanelService.onChangeForAllCheckbox(this.forAll);
  }

  removePageSet() {
    let id: string = this.leftPanelService.themeSettings.activeElem.id;
    let pageBody: any = {
      themeSetId: null
    }
    this.services.resource.updatePage(id, pageBody).subscribe((data) => {
      this.services.form.formObj.getPageById(id)['themeSetId'] = null;
      this.leftPanelService.removeSet('page');
      this.leftPanelService.switchToThemeCust();
    })
  }
}



