import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { LeftPanelService } from '../../left-panel.service';

@Component({
  selector: 'theme-border-setting',
  templateUrl: './border-setting.component.html',
  styleUrls: ['./border-setting.component.scss', '../../left-panel.component.scss']
})
export class BorderSettingComponent implements OnInit {
  @Input() borderInp: any;
  @Output() onChangeBorderDetails: EventEmitter<any> = new EventEmitter<any>();
  borderWidthConfig: any = {
    connect: [true,false],
    step: 1,
    range: {
      min: 0,
      max: 20
    }
  }
  borderStyleList: any;

  constructor(
    private lpService: LeftPanelService
  ) { }

  ngOnInit() {
    this.borderStyleList = this.lpService.borderStyleList;
    !this.borderInp.props.borderWidth && (this.borderInp.props['borderWidth'] = '0px');
    !this.borderInp.props.borderStyle && (this.borderInp.props['borderStyle'] = '');
    !this.borderInp.props.borderColor && (this.borderInp.props['borderColor'] = 'rgba(0,0,0,1)');
  }

  setColor(e: string){
    this.onChangeBorderDetails.emit({
      'field': this.borderInp.field,
      'subField': this.borderInp.subField,
      'cssProp': 'borderColor',
      'value': e
    })
  }

  setBorderStyle(val: string){
    this.onChangeBorderDetails.emit({
      'field': this.borderInp.field,
      'subField': this.borderInp.subField,
      'cssProp': 'borderStyle',
      'value': val
    })
  }

  setBorderWidth(val: string){
    this.onChangeBorderDetails.emit({
      'field': this.borderInp.field,
      'subField': this.borderInp.subField,
      'cssProp': 'borderWidth',
      'value': val + 'px'
    })
  }
}



