import { Component, OnInit } from '@angular/core';
import { EditDataService } from '../edit.data.service';
import { LeftPanelService } from './left-panel/left-panel.service';


@Component({
  selector: 'app-survey-themes',
  templateUrl: './survey.themes.component.html',
  styleUrls: ['../survey.edit.component.scss','./survey.themes.component.scss']
})
export class SurveyThemesComponent implements OnInit {
  surveyDesignData: any;
  themeId: any;
  setsBtn: any;

  constructor(
    private editDataService: EditDataService,
    private lpService: LeftPanelService
  ) { }

  ngOnInit() {
    this.surveyDesignData = this.editDataService.surveyDesignData;
    this.setsBtn = this.lpService.themeSetProperties.rightSideSetsBtn;
  }

  applyTheme(event) {
    this.themeId = event;
  }

}



