import { Component, OnInit, TemplateRef, ViewChild, Input, Output, EventEmitter } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

@Component({
  selector: 'theme-background-setting',
  templateUrl: './background-setting.component.html',
  styleUrls: ['./background-setting.component.scss', '../../left-panel.component.scss']
})
export class BackgroundSettingComponent implements OnInit {
  @Input() bgInpData;
  @Output() onChangeBgDetails: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild('uploadImageModal') uploadImageModal;

  imgName: string;
  modalRef: BsModalRef;
  regEx = /[\w0-9\-\_]*\.(png|jpg|gif|bmp|svg|jpeg|tiff)/;

  openModal(template: TemplateRef<any>, size: string) {
    this.modalRef = this.modalService.show(template, { class: size });
  }

  hideModal(): void {
    this.modalRef.hide();
  }

  constructor(
    private modalService: BsModalService
  ) { }

  ngOnInit() {
    this.imgName = (this.bgInpData.props.backgroundImage && this.bgInpData.props.backgroundImage.match(this.regEx)[0]);
  }

  openImageModal(){
    this.openModal(this.uploadImageModal, 'modal-lg');
  }

  setColor(e: string){
    this.onChangeBgDetails.emit({
      'field': this.bgInpData.field,
      'subField': this.bgInpData.subField,
      'cssProp': 'backgroundColor',
      'value': e
    })
  }

  onDoneImageSelect(e: any){
    this.hideModal();
    if(e){
      this.onChangeBgDetails.emit({
        'field': this.bgInpData.field,
        'subField': this.bgInpData.subField,
        'cssProp': 'backgroundImage',
        'value': 'url(' + e.url + ')'
      })
      this.imgName = this.bgInpData.props.backgroundImage.match(this.regEx)[0];
    }
  }

  removeBgImg(){
    this.onChangeBgDetails.emit({
      'field': this.bgInpData.field,
      'subField': this.bgInpData.subField,
      'cssProp': 'backgroundImage',
      'value': ''
    })
  }

}



