import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { LeftPanelService } from '../../left-panel/left-panel.service';
import { SharedServices } from '../../../../../../shared/services/services.module';

declare var QuestionTheme;

@Component({
  selector: 'theme-question-list',
  templateUrl: './theme-question-list.component.html',
  styleUrls: ['./theme-question-list.component.scss']
})
export class ThemeQuestionListComponent implements OnInit {
  @Input() themeId;
  @Input() count: number;
  @Input() questionId: string;

  setsBtn: any;
  surveyTheme: any;
  questionTheme: any;
  optionTheme: any;
  formData: any;

  constructor(
    private lpService: LeftPanelService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.formData = this.services.form.allQuestionsInFormGroup[this.questionId];
    this.applyTheme();
    this.applyOptSettings();
    this.setsBtn = this.lpService.themeSetProperties.rightSideSetsBtn;
  }

  ngOnChanges(changes: SimpleChanges) {
    if(this.lpService.getThemeCssObj()) {
      let questionThemeObj = this.lpService.themeJSON.question;
      let quesObj = this.services.form.formObj.getQuestionById(this.questionId);
      if(quesObj.themeSetId) {
        // if(this.services.form.formObj.theme.questionSet && this.services.form.formObj.theme.questionSet[quesObj.themeSetId]) {
        //   let questionSetTheme = this.services.form.formObj.theme.questionSet[quesObj.themeSetId];
        if(this.lpService.themeJSON.ifSetExist(quesObj.themeSetId)){
          let questionSetTheme = this.lpService.themeJSON.getThemeSet(quesObj.themeSetId);
          questionThemeObj = this.services.utils.extendObject(questionThemeObj, new QuestionTheme(questionSetTheme));
        }
      }
      this.questionTheme = this.services.form.formObj.theme.getFieldCssObj(questionThemeObj);
      this.surveyTheme = this.lpService.getThemeCssObj().survey;
      this.lpService.reflectOptionSettings();
    }
  }

  applyTheme() {
    this.lpService.themeCompState.subscribe(data => {
      if(!data) return;
      if(data.field && data.subField && data.field == 'survey' && data.subField == 'requiredAsk'){
        this.surveyTheme = this.lpService.getThemeCssObj().survey;
      } else if(data.field && 'question' === data.field) {
        let ques = this.services.form.formObj.getQuestionById(this.questionId);
        let setExists = this.lpService.themeJSON.ifSetExist(ques.themeSetId);
        if(data.forAll && !setExists) {
          this.questionTheme = this.lpService.getThemeCssObj().question;
        } else if (data.id == this.questionId) {
          this.questionTheme = this.lpService.themeSet[data.field];
        }
      }
    });
  }

  applyOptSettings(){
    this.lpService.optionsCompState.subscribe(data => {
      if(!data) return;
      this.optionTheme = this.lpService.themeJSON.getOptSettingCss();
      let quesObj = this.services.form.formObj.getQuestionById(this.questionId);
      let pageSetId:string = this.services.form.formObj.getPageByQuestionId(this.questionId)['themeSetId'];
      let color: string = quesObj.getOptionColor(this.lpService.themeJSON, pageSetId);
      this.optionTheme['color'] = color;
      // let bgColor: string =  quesObj.getOptionBGColor(this.lpService.themeJSON, pageSetId);
      // this.optionTheme['bgColor'] = bgColor;
    })
  }

  showQuesSets(id: string, e: any){
    e.stopPropagation();
    this.lpService.setComponent('question-sets', id);
  }

}



