import { NgModule } from '@angular/core';
import { SharedModule } from '../../../../shared/shared.module';
import { RouterModule, Routes } from '@angular/router';

import { SurveyThemesComponent } from './survey.themes.component';
import { ThemeSurveyListComponent } from './theme-survey-list/theme-survey-list.component';
import { ThemePageListComponent } from './theme-survey-list/theme-page-list/theme-page-list.component';
import { ThemeQuestionListComponent } from './theme-survey-list/theme-question-list/theme-question-list.component';
import { LeftPanelComponent } from './left-panel/left-panel.component';
import { ThemeCustomisationComponent } from './left-panel/theme-customisation/theme-customisation.component';
import { PageSettingComponent } from './left-panel/page-setting/page-setting.component';
import { ContainerSettingComponent } from './left-panel/container-setting/container-setting.component';
import { TextSettingComponent } from './left-panel/shared/text-setting/text-setting.component';
import { ThemesListComponent, FilterTheme } from './left-panel/themes-list/themes-list.component';
import { BackgroundSettingComponent } from './left-panel/shared/background-setting/background-setting.component';
import { FontSettingComponent } from './left-panel/shared/font-setting/font-setting.component';
import { ProgressBarComponent } from './left-panel/shared/progress-bar/progress-bar.component';
import { SurveySettingComponent } from './left-panel/survey-setting/survey-setting.component';
import { TitleSettingComponent } from './left-panel/title-setting/title-setting.component';
import { SliderComponent } from './left-panel/shared/slider/slider.component';
import { TextAlignComponent } from './left-panel/shared/text-align/text-align.component';
import { BorderSettingComponent } from './left-panel/shared/border-setting/border-setting.component';
import { DevicePreviewComponent } from './device-preview/device-preview.component';
import { FieldSetsComponent } from './left-panel/field-sets/field-sets.component';
import { OptionSettingsComponent } from './left-panel/theme-customisation/option-settings/option-settings.component'

import { LeftPanelService } from './left-panel/left-panel.service';
import { ThemeService } from './theme.service';


const surveyRoutes: Routes = [
  { path: '', component: SurveyThemesComponent},
  { path: 'preview', component: DevicePreviewComponent}
];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(surveyRoutes)
  ],
  exports: [

  ],
  declarations: [
    SurveyThemesComponent,
    ThemeSurveyListComponent,
    ThemePageListComponent,
    ThemeQuestionListComponent,
    ThemeSurveyListComponent,
    LeftPanelComponent,
    ThemeCustomisationComponent,
    PageSettingComponent,
    ContainerSettingComponent,
    TextSettingComponent,
    ThemesListComponent,
    FilterTheme,
    BackgroundSettingComponent,
    FontSettingComponent,
    ProgressBarComponent,
    SurveySettingComponent,
    TitleSettingComponent,
    SliderComponent,
    TextAlignComponent,
    BorderSettingComponent,
    DevicePreviewComponent,
    FieldSetsComponent,
    OptionSettingsComponent
  ],
  providers: [
    ThemeService,
    LeftPanelService
  ]
})
export class ThemesModule { }



