import { Component, OnInit } from '@angular/core';
import { LeftPanelService } from '../../left-panel.service';

@Component({
  selector: 'theme-option-settings',
  templateUrl: './option-settings.component.html',
  styleUrls: ['./option-settings.component.scss', '../../left-panel.component.scss']
})
export class OptionSettingsComponent implements OnInit {
  optionsJSON: any;

  constructor(
    private leftPanelService: LeftPanelService
  ) { }

  ngOnInit() {
    this.optionsJSON = this.leftPanelService.themeJSON.options;
  }

  setNpsColor(e: string, field: string, type: string){
    this.optionsJSON.nps[field][type] = e;
    this.onChange();
  }

  setNpsNoColor(e: string, type: string){
    for(let noColIdx = 0; noColIdx < 11; noColIdx++){
      this.optionsJSON.nps.noColor[noColIdx][type] = e;
    }
    this.onChange();
  }

  setGraphRatingColor(e: string){
    this.optionsJSON.graphRating.color = e;
    this.onChange();
  }

  setSelectedOpt(e: string, type: string){
    this.optionsJSON.selectedOpt[type] = e;
    this.onChange();
  }

  onChange(){
    this.leftPanelService.reflectOptionSettings();
  }

}



