import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { LeftPanelService } from '../../left-panel.service';

@Component({
  selector: 'theme-font-setting',
  templateUrl: './font-setting.component.html',
  styleUrls: ['./font-setting.component.scss', '../../left-panel.component.scss']
})
export class FontSettingComponent implements OnInit {
  @Input() fontInpData;
  @Output() onChangeFontDetails: EventEmitter<any> = new EventEmitter<any>();

  fontFamList: any = [];

  constructor(
    private lpService: LeftPanelService
  ) { }

  ngOnInit() {
    this.fontFamList = this.lpService.fontFamList;
    !this.fontInpData.props.fontFamily && (this.fontInpData.props.fontFamily = '');
  }

  setColor(e: string){
    this.onChangeFontDetails.emit({
      'field': this.fontInpData.field,
      'subField': this.fontInpData.subField,
      'cssProp': 'color',
      'value': e
    })
  }

  setFontFam(val: string){
    this.onChangeFontDetails.emit({
      'field': this.fontInpData.field,
      'subField': this.fontInpData.subField,
      'cssProp': 'fontFamily',
      'value': val
    })
  }

}



