import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'theme-progress-bar',
  templateUrl: './progress-bar.component.html',
  styleUrls: ['./progress-bar.component.scss', '../../left-panel.component.scss']
})
export class ProgressBarComponent implements OnInit {
  @Input() progressBarData;
  @Output() onChangeProgBar: EventEmitter<any> = new EventEmitter<any>();
  progBarConfig: any = {
    connect: [true,false],
    step: 1,
    range: {
      min: 1,
      max: 100
    }
  }
  progBarValue: number;

  constructor() { }

  ngOnInit() {
    this.progBarValue = this.progressBarData.props.width;
  }

  setColor(e: string){
    this.onChangeProgBar.emit({
      'field': this.progressBarData.field,
      'subField': this.progressBarData.subField,
      'cssProp': 'color',
      'value': e
    })
  }

  getProgressValue(e: any){
    this.progBarValue = e;
    this.onChangeProgBar.emit({
      'field': this.progressBarData.field,
      'subField': this.progressBarData.subField,
      'cssProp': 'width',
      'value': e + '%'
    })
  }

}



