import { Component, OnInit } from '@angular/core';
import { LeftPanelService } from '../../left-panel/left-panel.service';
import { SharedServices } from '../../../../../../shared/services/services.module';

@Component({
  selector: 'theme-survey-setting',
  templateUrl: './survey-setting.component.html',
  styleUrls: ['./survey-setting.component.scss', '../left-panel.component.scss']
})
export class SurveySettingComponent implements OnInit {
  themeJSON: any;
  themeName: string;

  constructor(
    private leftPanelService: LeftPanelService,
    private services: SharedServices
  ) { }

  ngOnInit() {
    this.themeJSON = this.leftPanelService.themeJSON;
    this.themeName = this.themeJSON.name;
    !this.themeJSON.survey.body.fontSize && (this.themeJSON.survey.body.setFontSize("16px"));
  }

  onChange(event: any){
    this.leftPanelService.callNextOnThemeCompState(event);
  }

  updateTheme() {
    let forAll = true;
    this.leftPanelService.updateTheme(forAll).subscribe( data => {
      this.cancelCustomisation();
    });
  }

  cancelCustomisation(){
    this.leftPanelService.onCancelCustomise();
    this.leftPanelService.switchToThemeCust();
  }
}



