import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { LeftPanelService } from '../../left-panel.service';

@Component({
  selector: 'theme-text-setting',
  templateUrl: './text-setting.component.html',
  styleUrls: ['./text-setting.component.scss', '../../left-panel.component.scss']
})
export class TextSettingComponent implements OnInit {
  @Input() textInpData;
  @Output() onChangeTextDetails: EventEmitter<any> = new EventEmitter<any>();

  fontSizeConfig: any = {
    connect: [true,false],
    step: 2,
    range: {
      min: 8,
      max: 48
    }
  }

  constructor(
    private leftPanelService: LeftPanelService
  ) { }

  ngOnInit() {
  }

  getFontDetails(e: any){
    this.onChangeTextDetails.emit({
      'field': this.textInpData.field,
      'subField': this.textInpData.subField,
      'cssProp': e.cssProp,
      'value': e.value
    })
  }

  getProgressValue(e: any){
    this.onChangeTextDetails.emit({
      'field': this.textInpData.field,
      'subField': this.textInpData.subField,
      'cssProp': 'fontSize',
      'value': e + 'px'
    })
  }

  setStyle(e: any, type: string){
    let styleVal: string;
    if(type == 'fontWeight'){
      styleVal = e.target.checked ? 'bold' : 'unset';
    }else if(type == 'fontStyle'){
      styleVal = e.target.checked ? 'italic' : 'unset';
    }else if(type == 'textDecoration'){
      styleVal = e.target.checked ? 'underline' : 'unset';
    }
    this.onChangeTextDetails.emit({
      'field': this.textInpData.field,
      'subField': this.textInpData.subField,
      'cssProp': type,
      'value': styleVal
    })
  }

  getProgressAlign(e: any){
    this.onChangeTextDetails.emit({
      'field': this.textInpData.field,
      'subField': this.textInpData.subField,
      'cssProp': 'textAlign',
      'value': e
    })
  }

}



