import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { LeftPanelService } from '../left-panel.service';
import { SharedServices } from '../../../../../../shared/services/services.module';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

declare var Theme;

@Component({
  selector: 'theme-customisation',
  templateUrl: './theme-customisation.component.html',
  styleUrls: [
    './theme-customisation.component.scss',
    '../left-panel.component.scss'
  ]
})
export class ThemeCustomisationComponent implements OnInit {
  themeJSON: any;
  saveAsThemeObj: any = {
    action: false,
    name: '',
    themeObj: ''
  }
  fontSizeConfig: any = {
    connect: [true,false],
    step: 2,
    range: {
      min: 8,
      max: 48
    }
  }

  @ViewChild('themeCopyInp') themeCopyInp;
  @ViewChild('selFolderModal') selFolderModal;
  modalRef: BsModalRef;

  openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template, { class: 'modal-lg' });
  }

  hideModal(): void {
    this.modalRef.hide();
  }

  constructor(
    private leftPanelService: LeftPanelService,
    private services: SharedServices,
    private modalService: BsModalService
  ) { }

  ngOnInit() {
    this.themeJSON = this.leftPanelService.themeJSON;
    !this.themeJSON.survey.requiredAsk.fontSize && (this.themeJSON.survey.requiredAsk.fontSize = '14px');
  }

  onChange(e: any){
    this.leftPanelService.callNextOnThemeCompState(e);
  }

  saveOrUpdateTheme(){
    if(!this.themeJSON.name || this.themeJSON.name.trim() == ''){
      this.services.toastr.error('Theme Name is Required');
      return false;
    }
    if(this.themeJSON._id){
      this.updateTheme();
    }else{
      this.openModal(this.selFolderModal);
    }
  }

  saveTheme(e: any){
    this.hideModal();
    if(this.saveAsThemeObj.action){ //if theme is under saveAs operation
      this.themeJSON.name = this.saveAsThemeObj.name;
      delete this.themeJSON._id;
      delete this.themeJSON.owner;
    }
    this.leftPanelService.saveTheme(e).subscribe(data => {
      if(data['data']) {
        this.leftPanelService.allThemesArr.list.push(data['data']);
        this.leftPanelService.allThemesArr.count++;
        this.themeJSON = this.leftPanelService.themeJSON;
        this.services.toastr.success('Theme has been saved successfully.', 'Success');
      }
      this.leftPanelService.resetToThemeList();
    });
  }

  updateTheme(){
    this.leftPanelService.updateTheme(true).subscribe((data) => {
      let idx: number = this.services.utils.findIndex(this.leftPanelService.allThemesArr.list, '_id', data.data._id);
      this.leftPanelService.allThemesArr.list.splice(idx, 1, data.data);
      this.services.toastr.success('Theme has been updated successfully.', 'Success');
      this.cancelCustomisation();
    })
  }

  cancelCustomisation(){
    this.leftPanelService.onCancelCustomise();
    this.leftPanelService.resetToThemeList();
  }

  setPropertyValue(field: string, subField: string, cssProp: string, e: any){
    this.onChange({
      'field': field,
      'subField': subField,
      'cssProp': cssProp,
      'value': e
    })
  }

  saveAsTheme(){
    this.saveAsThemeObj.action = true;
    setTimeout(() => {
      this.themeCopyInp.nativeElement.select();
    },0);
    this.saveAsThemeObj.name = this.themeJSON.name + '_copy';

  }

  cancelCopyTheme(){
    this.saveAsThemeObj.action = false;
    this.saveAsThemeObj.name = '';
  }

  saveCopyTheme(){
    this.openModal(this.selFolderModal);
  }

  onKeyUpInSavingTheme(e: any){
    if(e.which == 13){
      this.saveCopyTheme();
    }
  }

}



