import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { LeftPanelService } from './left-panel.service';

@Component({
  selector: 'theme-left-panel',
  templateUrl: './left-panel.component.html',
  styleUrls: ['./left-panel.component.scss']
})
export class LeftPanelComponent implements OnInit {
  @Output() onThemeApply: EventEmitter<any> = new EventEmitter<any>();
  themeSettings: any
  activeElem: any;

  constructor(
    private leftPanelService: LeftPanelService
  ) { }

  ngOnInit() {
    this.themeSettings = this.leftPanelService.themeSettings;
    setTimeout(() => {
      this.themeSettings.presentAction = 'themes-list';
      this.leftPanelService.checkEventBinding();
    },1);
  }

}



